body {
    margin: 0;
    padding: 0;
    font-family: 'Courier New', Courier, monospace;
}

#container {
    display: grid;
    grid-template-rows: 80px 1fr 50px;
    min-height: 100vh;
}

header {
    display: flex;
    box-sizing: border-box;
    padding-left: 30px;
    gap: 30px;
    background-color: lightblue;
    border-bottom: 3px solid lightsteelblue;
    align-items: center;
}

#arrow {
    border: solid black;
    border-width: 0 5px 5px 0;
    display: inline-block;
    padding: 5px;
    background: transparent;
    cursor: pointer;
}

.down {
    transform: rotate(45deg);
}

.right {
    transform: rotate(-45deg);
}


main {
    display: flex;
}

.hide-sidebar>#sidebar {
    display: none;
}

#sidebar {
    background-color: rgb(240, 240, 240);
    padding: 30px;
    min-width: 300px;
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    gap: 60px;
}

#sidebar>.tabs-wrapper {
    display: flex;
    flex-direction: column;
    gap: 30px;
}

.tabs-wrapper>.tab {
    display: flex;
    align-items: center;
    box-sizing: border-box;
    padding-left: 5px;
    gap: 20px;
    background-color: white;
    border-radius: 10px;
    border: 3px solid whitesmoke;
}

.tabs-wrapper>.tab>img {
    height: 50px;
}

.tab:hover {
    cursor: pointer;
    border: 3px solid lightsteelblue !important;
}

.tab.selected {
    background-color: lightblue !important;
    border: 3px solid lightsteelblue !important;
}

#sidebar>.projects {
    display: flex;
    flex-direction: column;
}

#sidebar>.projects>.header {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.projects button {
    border: none;
    appearance: none;
    background-color: inherit;
}

.projects button>img {
    height: 20px;
}

.projects button:hover {
    cursor: pointer;
    transition: all ease-in-out;
    transform: scale(1.2);
}

button.tooltip>img {
    height: 30px;
}

.tooltip {
    position: relative;
}

.tooltip::before {
    background-color: white;
    border: 2px solid lightsteelblue;
    border-radius: 5px;
    width: 100px;
    content: attr(data-title);
    visibility: hidden;
    font-family: 'Courier New', Courier, monospace;
    padding: 3px;
    position: absolute;
    bottom: 115%;
    right: 0;
    z-index: 1;
}

.tooltip::after {
    position: absolute;
    left: 50%;
    bottom: 85%;
    margin-left: -6px;
    border: 6px solid;
    border-color: lightsteelblue transparent transparent transparent;
    content: "";
    visibility: hidden;
}

.tooltip:hover::before,
.tooltip:hover::after {
    visibility: visible;
}

#dialog {
    width: 300px;
    height: 200px;
    border: 5px solid lightsteelblue;
    border-radius: 5px;
    padding: 0;
}

#dialog>.wrapper>.header {
    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
    height: 50px;
    border-bottom: 3px solid;
    border-color: lightsteelblue;
    background-color: lightblue;
}

#dialog>.wrapper>.main {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100px;
}

input#projectTitle {
    padding: 5px;
}

#dialog>.wrapper>.footer {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    box-sizing: border-box;
    padding-right: 25px;
    gap: 20px;
    height: 50px;
    border-top: 3px solid;
    border-color: lightsteelblue;
    background-color: lightblue;
}

#dialog>.wrapper>.footer>button {
    border: 3px solid lightsteelblue;
    border-radius: 10px;
    height: 30px;
}

#projectConfirm {
    background-color: greenyellow;
}

#projectCancel {
    background-color: rgb(255, 130, 130);
}

#newProjects {
    display: flex;
    flex-direction: column;
    gap: 15px;
}

#newProjects>div {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: white;
    padding-left: 10px;
    border-radius: 10px;
    border: 3px solid whitesmoke;
    box-sizing: border-box;
}

#content {
    width: 100%
}

footer {
    background-color: lightblue;
    border-top: 3px solid lightsteelblue;
    display: flex;
    align-items: center;
    justify-content: center;
    box-sizing: border-box;
    gap: 10px;
}

footer>a>img {
    height: 25px;
}

footer>a>img:hover {
    transition: all ease-in-out;
    transform: scale(1.2);
}

@media screen and (max-width: 600px) {
    #sidebar {
        /* position: fixed; */
        z-index: 1;
    }
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA;;IAEI,SAAS;IACT,UAAU;IACV,8CAA8C;AAClD;;AAEA;IACI,aAAa;IACb,iCAAiC;IACjC,iBAAiB;AACrB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,kBAAkB;IAClB,SAAS;IACT,2BAA2B;IAC3B,uCAAuC;IACvC,mBAAmB;AACvB;;AAEA;IACI,mBAAmB;IACnB,yBAAyB;IACzB,qBAAqB;IACrB,YAAY;IACZ,uBAAuB;IACvB,eAAe;AACnB;;AAEA;IACI,wBAAwB;AAC5B;;AAEA;IACI,yBAAyB;AAC7B;;;AAGA;IACI,aAAa;AACjB;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,oCAAoC;IACpC,aAAa;IACb,gBAAgB;IAChB,aAAa;IACb,sBAAsB;IACtB,sBAAsB;IACtB,SAAS;AACb;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,SAAS;AACb;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,sBAAsB;IACtB,iBAAiB;IACjB,SAAS;IACT,uBAAuB;IACvB,mBAAmB;IACnB,4BAA4B;AAChC;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,eAAe;IACf,2CAA2C;AAC/C;;AAEA;IACI,sCAAsC;IACtC,2CAA2C;AAC/C;;AAEA;IACI,aAAa;IACb,sBAAsB;AAC1B;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,8BAA8B;AAClC;;AAEA;IACI,YAAY;IACZ,gBAAgB;IAChB,yBAAyB;AAC7B;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,eAAe;IACf,2BAA2B;IAC3B,qBAAqB;AACzB;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,kBAAkB;AACtB;;AAEA;IACI,uBAAuB;IACvB,gCAAgC;IAChC,kBAAkB;IAClB,YAAY;IACZ,yBAAyB;IACzB,kBAAkB;IAClB,8CAA8C;IAC9C,YAAY;IACZ,kBAAkB;IAClB,YAAY;IACZ,QAAQ;IACR,UAAU;AACd;;AAEA;IACI,kBAAkB;IAClB,SAAS;IACT,WAAW;IACX,iBAAiB;IACjB,iBAAiB;IACjB,gEAAgE;IAChE,WAAW;IACX,kBAAkB;AACtB;;AAEA;;IAEI,mBAAmB;AACvB;;AAEA;IACI,YAAY;IACZ,aAAa;IACb,gCAAgC;IAChC,kBAAkB;IAClB,UAAU;AACd;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,sBAAsB;IACtB,YAAY;IACZ,wBAAwB;IACxB,4BAA4B;IAC5B,2BAA2B;AAC/B;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,aAAa;AACjB;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,aAAa;IACb,yBAAyB;IACzB,mBAAmB;IACnB,sBAAsB;IACtB,mBAAmB;IACnB,SAAS;IACT,YAAY;IACZ,qBAAqB;IACrB,4BAA4B;IAC5B,2BAA2B;AAC/B;;AAEA;IACI,gCAAgC;IAChC,mBAAmB;IACnB,YAAY;AAChB;;AAEA;IACI,6BAA6B;AACjC;;AAEA;IACI,oCAAoC;AACxC;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,SAAS;AACb;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,8BAA8B;IAC9B,uBAAuB;IACvB,kBAAkB;IAClB,mBAAmB;IACnB,4BAA4B;IAC5B,sBAAsB;AAC1B;;AAEA;IACI;AACJ;;AAEA;IACI,2BAA2B;IAC3B,oCAAoC;